body {
  background-color: #e0e0e0;
}
.movies {
  padding: 3em;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 6em;
  grid-auto-rows: minmax(100px, auto);
}
.movie {
  padding: 1em;
  background-color: #fff;
  border-radius: 6px;
  box-shadow: 6px 6px 6px 2px #00000035;
  display: grid;
  grid-template-columns: 180px auto;
  grid-gap: 1em;
  /* grid-auto-rows: minmax(1em, auto); */
  grid-template-rows: minmax(1em, 4.5em) 1em minmax(1em, auto) 1em auto;
}
.movie__img {
  position: relative;
  top: -3em;
  width: 150px;
  grid-row: 1/4;
  grid-column: 1;
  box-shadow: 6px 6px 12px 3px #00000020;
}
.movie__title {
  grid-row: 1;
  grid-column: 2;
}
.movie__title a {
  text-decoration: none;
  color: #222;
  font-weight: bold;
  font-family: sans-serif;
}
.movie__year {
  grid-row: 2;
  grid-column: 2;
  margin: 0;
  font-size: 1em;
  font-weight: normal;
}
.movie__summary {
  grid-row: 3;
  grid-column: 2;
}
.movie__genres {
  grid-row: 4;
  grid-column: 2;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.movie__genres li {
  display: inline-block;
  font-size: 0.9em;
  margin-right: 0.5em;
}
